body{
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #1e1e1e;
  color: #f5f5f5;
}

.App{
  text-align: center;
  padding: 20px;
}

.App-header{
  background-color: #282c34;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.5);
}

h1{
  font-size: 2.5rem;
  color: #61dafb;
}

h2{
  margin-top: 20px;
  color: #ffcc00;
}

ul{
  list-style-type: none;
  color: #ffcc00;
}

input[type="text"] {
  padding: 10px;
  margin-top: 20px;
  border-radius: 5px;
  border: none;
  background-color: #444;
  color: white;
  font-size: 1rem;
  width: 200px;
}

button {
  padding: 10px 20px;
  margin-top: 10px;
  background-color: #61dafb;
  color: #282c34;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

button:hover {
  background-color: #21a1f1;
}

p{
  font-size: 1.2rem;
  color: #ff7777;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.chart-container {
  width: 100%;
  max-width: 800px;
  margin: 20px 0;
}

ul {
  max-width: 800px;
  width: 100%;
  text-align: left;
}

@media (max-width: 768px) {
  .chart-container {
    width: 100%;
  }
}
